.searchPage a{
    /* width: 80vw;
    max-width: 800px; */
    text-decoration: none;
}
.searchPage__logo{
 object-fit: contain;
 /* width: 200px; */
 height: 50px;
 margin-right: 3rem;
 /* text-align: center; */
}

.searchPage__header{
    display: flex;
    /* flex-direction: row; */
    /* gap: 2rem; */
    align-items: flex-start;
    /* justify-content: center; */
    position: sticky;
    top: 0;
    background: white;
    padding: 2rem;
    border-bottom: 2px solid lightgray;

    z-index: 100;
}

.searchPage__headerBody > .search > .search__input{
    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
}

.searchPage__options{
    display: flex;
    align-items: center;
    color: gray;
}

.searchPage__optionsRight,
.searchPage__optionsLeft{
    display: flex;
}

.searchPage__optionsRight a,
.searchPage__optionsLeft a{
    color: grey;
}

.searchPage__optionsRight{
    margin-left: 5rem;
}
.searchPage__option{
    display: flex;
    align-items: center;
    color: gray;
    margin-left: 1rem;
    margin-top: 1rem;

}
.searchPage__results{
    text-align: left;
    max-width: 650px;
    margin-top: 1rem;
    margin-left: 5rem;
    /* margin: auto; */
}
.searchPage__result{
    margin: 2rem 0;
    border-bottom: 1px solid rgba(175, 175, 175, 0.542);
    padding-bottom: 1rem;
}

.searchPage__resultCount{
    color: #70757a;
    font-size: .8rem;
}


.searchPage__resultTitle > h2{
    font-weight: 500;
}

.searchPage__resultSnippet{
    margin-top: 0.5rem;
}

.searchPage__resultImage{
    object-fit: contain;
    height: 20px;width: 20px;
    margin-right: 1rem;
}