.search__input{
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    height: 30px;
    padding: 1rem 2rem;
    border-radius: 5rem;
    margin: auto;
    margin-top: 40px;
    max-width: 560px;
    width: 75vw;
    justify-content: space-between;
}
.search__input > input{
    flex: 1;
    padding: 1rem;
    font-size: 1rem;
    border: none;
    outline: none;
}

.search__buttons{
    margin-top: 10%;
    display: flex;
    /* gap: 2rem; */
    justify-content: center;
}
.search__buttons button{
    color: gray;
    border: none;
}
.search__buttons button:hover{
    border: none;
}